.title {
  display: flex;
  align-items: flex-end;
  color: var(--text-color);
  height: 25px;
  font-size: 16px;
  font-weight: 300;
  margin: 4px 30px 4px 0;
  transition: all 0.1s;
  cursor: pointer;
}

.currentTitle {
  color: var(--title-color);
  font-size: 18px;
  font-weight: 500;
}